import React, {useState} from 'react';



function TotalCounter(props) {
    const [count, clearTotalCount] = useState(props.startCount)
    const clearCount =() => {
        clearTotalCount(0);
    }
    return (
        <div>
            <button onClick={clearCount}>Reset Total</button>


        </div>

    );
}
export default TotalCounter;
